.error-box {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 20px;
    background: linear-gradient(to bottom right, #34d399, #6366f1); /* Gradient color */
    border: 1px solid #2c3e50; /* Border color */
    border-radius: 5px;
    text-align: center;
    color: #fff; /* Text color */
  }
  
  .return-button {
    margin-top: 10px;
    padding: 8px 16px;
    background-color: #000; /* Black color */
    color: #fff; /* Text color */
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  